.tile{
    display: inline-flex;
    background-repeat: no-repeat;
    background-size: contain;
    width: 40px;
    height: 40px;
}


.finish{
    background-image: url('/tiles/finish.png');
}

.default {

}

.world{
    background: url('/tiles/road_0.png') no-repeat 0 0;
    background-size: cover;
}

.stones{
    position: relative;
}

.pause-screen-wrapper{
    width: 100%;
    height: 100%;
    text-align: center;
    position: relative;
}

.pause-screen{
    position: absolute;
    height: 55px;
    top: 30%;
    width: 100%;
}

.end{
    color: white;
    text-align: center;
    top: 159px;
    position: absolute;
}